// Veronika
.hotel-gallery-double {
    background-color: var(--background-main-color);
}

.hotel-gallery-double-container {
    position: relative;
    padding-top: 32px;
    padding-bottom: 32px;
}

// tablet
@media screen and (min-width: 1024px) {
    .hotel-gallery-double-container {
        padding-top: 64px;
        padding-bottom: 104px;
    }
}

// desktop
@media screen and (min-width: 1280px) {
    .hotel-gallery-double-container {
        padding-top: 72px;
        padding-bottom: 104px;
    }
}


// logo+text
.hotel-room-two-logo-box {
    display: inline-block;
    margin-bottom: 32px;
}

.hotel-room-two-logo-text {
    margin-bottom: 8px;
    font-family: var(--title-font);
    font-size: 20px;
    line-height: 1.1;

    color: var(--subtitle-color);
}

.hotel-room-two-logo {
    font-family: 'FilmP3',
        sans-serif;
    font-size: 40px;
    line-height: 1.1;

    letter-spacing: 0.3em;

    color: var(--logo-dark-color);
}

// tablet
@media screen and (min-width: 1024px) {
    .hotel-room-two-logo-box {
        margin-bottom: 62px;
        padding-top: 0;
        width: 304px;
    }

    .hotel-room-two-logo-text {
        margin-bottom: 4px;
        font-size: 32px;
    }

    .hotel-room-two-logo {
        font-size: 64px;
    }
}

// desktop
@media screen and (min-width: 1280px) {
    .hotel-room-two-logo-box {
        margin-bottom: 62px;
    }
}


// title+text
.hotel-room-two-title-box {
    margin-bottom: 32px;
}

.hotel-room-two-title {
    margin-bottom: 12px;

    font-family: var(--title-font);
    font-size: 24px;
    line-height: 1.2;

    letter-spacing: 0.05em;
}

.hotel-room-two-subtitle {
    width: 258px;

    font-family: var(--text-font);
    font-weight: 600;
    font-size: 12px;
    line-height: 1.2;

    letter-spacing: 0.02em;

    color: var(--text-main-color);
}

// tablet 
@media screen and (min-width: 1024px) {
    .hotel-room-two-title-box {
        margin-bottom: 174px;
    }

    .hotel-room-two-title {
        margin-bottom: 16px;
        margin-left: 0;

        font-size: 30px;
        line-height: 1.1;
    }

    .hotel-room-two-subtitle {
        width: 439px;

        font-size: 16px;
        line-height: 1.1;
    }
}

// desktop
@media screen and (min-width: 1280px) {
    .hotel-room-two-title-box {
        width: 400px;
    }

    .hotel-room-two-title {
        margin-bottom: 24px;

        font-size: 36px;
    }

    .hotel-room-two-subtitle {
        width: 400px;
    }
}

// img-room
.hotel-room-two-img {
    margin-left: auto;
    margin-bottom: 20px;

    box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
    border-radius: 2px;
}

// tablet
@media screen and (min-width: 1024px) {
    .hotel-room-two-img {
        position: absolute;
        top: -32px;
        right: 112px;
        margin-bottom: 0;

        box-shadow: 5px 5px 70px 17px rgba(0, 0, 0, 0.25);
        border-radius: 0px;
    }
}

// desktop
@media screen and (min-width: 1280px) {
    .hotel-room-two-img {
        position: absolute;
        right: 180px;

        border-radius: 2px;
    }
}

// slider
.hotel-room-two-slider-list {
    display: none;
}

.hotel-room-two-slider-img {
    box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
}

// tablet
@media screen and (min-width: 1024px) {
    .hotel-room-two-slider-list {
        // margin-top: 174px;
        display: flex;
    }

    .hotel-room-two-slider-li:not(:last-child) {
        margin-right: 40px;
    }
}

// desktop


// buttons
.hotel-room-two-btn-list {
    margin: 0 auto;

    text-align: center;
}

.hotel-room-two-btn {
    padding-top: 14px;
    padding-bottom: 14px;
    width: 236px;
    height: 48px;

    font-family: var(--text-font);
    font-weight: 700;

    letter-spacing: 0.02em;

    color: var(--accent-yellow);
    background-color: var(--main-black);
    border-radius: 25px;
    border: 2px solid #FFFDEE;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.hotel-room-two-btn:hover,
.hotel-room-two-btn:focus {
    background: var(--main-white);
    color: var(--accent-yellow);

    border: 2px solid #F3C653;

    cursor: pointer;
}

.hotel-room-two-btn-link {
    display: inline-block;
    margin-bottom: 16px;
    padding-top: 14px;
    padding-bottom: 14px;
    width: 236px;
    height: 48px;

    font-family: var(--text-font);
    font-weight: 700;

    letter-spacing: 0.02em;

    color: var(--accent-yellow);
    background-color: var(--main-black);
    border-radius: 25px;
    border: 2px solid #FFFDEE;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.hotel-room-two-btn-link:hover,
.hotel-room-two-btn-link:focus {
    background: var(--main-white);
    color: var(--accent-yellow);

    border: 2px solid #F3C653;
    box-shadow: none;
}

// tablet
@media screen and (min-width: 1024px) {
    .hotel-room-two-btn-list {
        position: absolute;
        top: 581px;
        right: 47px;
        margin: 0;
        padding-bottom: 0;
    }

    .hotel-room-two-btn-link {
        display: block;
    }
}

// desktop
@media screen and (min-width: 1280px) {
    .hotel-room-two-btn-list {
        position: absolute;
        top: 662px;
        right: 85px;
        margin: 0;
    }
}