.overlay {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
  -webkit-backdrop-filter: blur(2px);
  backdrop-filter: blur(2px);
  z-index: 20;
  transition: var(--animation);
  transition: opacity var(--animation), visibility var(--animation);
  background: rgba(255, 253, 238, .5);
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  max-width: 500px;
  z-index: 30;
  text-align: center;
  background-color: #fff;
  border-radius: 3px;
  padding: 30px;
  transition: all .3s;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 3px 10px -.5px rgba(0, 0, 0, .2);
}

.modal.active, .overlay.active {
  opacity: 1;
  visibility: visible;
}

.modal__cross {
  width: 15px;
  height: 15px;
  fill: #444;
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 20px;
}

.modal {
  width: calc(100% - 18px);
  height: auto;
  max-height: 80vh;
  opacity: 1;
  transition: transform var(--animation), opacity var(--animation);
  background: #fffdee;
